import { ethers } from 'ethers';

// 
export const formatHash = (hash: string): string => {
  return `${hash.slice(0, 6)}...${hash.slice(-4)}`;
};

// 
export const formatCallResult = (data: unknown): string => {
  if (data === null || data === undefined) {
    return '';
  }

  if (typeof data === 'object') {
    if (data instanceof ethers.Contract) {
      return '';
    }
    return JSON.stringify(data, null, 2);
  }

  return String(data);
};

// 
export const getTypeDescription = (type: string): string => {
  switch (type) {
    case 'uint256':
    case 'uint':
      return ': 123';
    case 'int256':
    case 'int':
      return ': -123';
    case 'address':
      return ': 0x1234...';
    case 'bool':
      return ': true  false';
    case 'string':
      return '';
    case 'bytes':
      return '';
    default:
      if (type.endsWith('[]')) {
        return '';
      }
      return '';
  }
};

// 
export const getTypeHint = (type: string): string => {
  switch (type) {
    case 'uint256':
    case 'uint':
    case 'int256':
    case 'int':
      return '';
    case 'address':
      return ' 0x ';
    case 'bool':
      return ' true  false';
    case 'string':
      return '';
    case 'bytes':
      return '';
    default:
      if (type.endsWith('[]')) {
        return '';
      }
      return '';
  }
}; 